#root {
  background: rgb(30,30,30);
}

.App {
  margin: 0 auto;
  max-width: 2160px;
  height: 100vh;
  display:flex;
  flex-direction: row;
  justify-content: flex-start;
}

#page-body {
  width: 100%;
  margin: 0 auto;
  padding: 30px;
  padding-right: 75px;
  flex-grow: 5;
  padding-top: 20px;
  background-image:linear-gradient(rgb(30,30,30) 30%, 90%, rgb(60,60,60)); 
  overflow-y: auto;
}

body {
  background: none;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  color: lightgray;
}

h3, h4 {
  margin-bottom: 0;
}

.reflection a {
  color: lightgreen;
  text-decoration-line: none;
}

.reflection a:hover {
  color: lightgoldenrodyellow;
}

.nav-root, .nav-root-close {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  height: 100vh;
  width: 300px;
  max-width: 300px;
  background:rgb(30,30,30);
  box-shadow: 5px, 5px, 5px, 5px, white;
  z-index: 5;
  transition: 50ms ease;
}

.nav-root-close {
  transition: 50ms ease;
  visibility: hidden;
  max-width: 0;
}

.nav-homebar {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-content: center;
  gap: 50px;
  padding-top: 10px;
  padding-left: auto;
  padding-right: auto;
  padding-bottom: 100px;
  width: 50px;
  height: 100vh;
  background-image: linear-gradient(90deg, transparent 90%, black 100%), url(./assets/pictures/black-perf-tile.png);
}

.nav-homebar-nameplate {
  font-family: Rubik Mono One;
  flex-basis: 300px;
  writing-mode:sideways-lr;
  margin-top: 0;
  margin-bottom: auto;
  margin-left: auto;
  margin-right: aut0;
  width: 100%;
  text-wrap:nowrap;
}

.nav-homebar-nameplate h3 {
  color: rgb(30,30,30);
  font-size: 28px;
  text-shadow: 2px 2px black;
  margin: 0;
  padding: 0;
  -webkit-text-fill-color: rgb(150,150,150);
  -webkit-text-stroke: 1px;
}


.nav-homebar-button, #button-close {
  border: 1px solid black;
  margin: auto auto;
  padding: auto auto;
  width: 30px;
  height: 30px;
  align-items: center;
  border-radius: 3px;
  white-space: nowrap;
  overflow:hidden;
}

.nav-homebar-button:hover, #button-close:hover {
  box-shadow: 0px 0px 10px rgba(255,255,255, 0.7);
}

.nav-homebar-button:active, #button-close:active {
  box-shadow: inset 0px 0px 2px rgba(0,0,0, 1);
  padding: 5px 5px;
}

.nav-homebar-button:active img, #button-close:active img{
  opacity: 60%;
}

#button-github {
  background: linear-gradient(rgba(255, 255, 255, 0.35) 10%, transparent 30%), #3f2c3f;
}

#button-linkedin {
  background: linear-gradient(rgba(255, 255, 255, 0.35) 10%, transparent 30%), #2871aa;
}

#button-mail {
  background: linear-gradient(rgba(255, 255, 255, 0.35) 10%, transparent 30%), #c39500;
}

#button-close {
  position: absolute;
  width: 50px;
  height: 50px;
  margin-right: 25px;
  margin-bottom: 25px;
  right: 0px;
  bottom: 0px;
  background: linear-gradient(rgba(255, 255, 255, 0.35) 10%, transparent 30%), #990000;
}

nav {
  display: flex;
  flex-direction: column;
  width: 22.5vw;
  background:
    linear-gradient(
      200deg,
      transparent 80%,
      rgba(255, 255, 255, 0.2) 100%
    ),
    linear-gradient(
    90deg,
    rgb(100,100,100) 0%,  
    rgb(30, 30, 30) 3%,
    rgb(30, 30, 30) 97%,
    rgb(100,100,100) 100%
    );
}

.nav-profile {
  margin: 20px auto;
  display: flex;
  max-width: 150px;
  max-height: 150px; 
  overflow:hidden;
  outline: 2px solid rgb(40,40,40);
  border: 5px solid rgb(60,60,60);
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.8), 0 6px 20px 0 rgba(0, 0, 0, 0.8);
}

nav ul {
  margin: 0 auto;
  list-style-type: none;
  padding-left: 0;
  width: 100%;
  overflow: hidden;
}

nav li {
  background: linear-gradient(90deg, slategray 0%, black 3%, black 98%, slategray 99%); 
  width: 100%;
  text-align: right;
  border-top: 1px outset #F5F5F5;
  border-bottom: 3px outset #F5F5F5;
  font-family: 'Rubik Mono One';
  font-size: 16px;
  overflow: hidden;
}

nav li:hover {
  background: linear-gradient(90deg, slategray 0%, rgb(60, 60, 60) 3%, rgb(60, 60, 60) 98%, slategray 99%); 
}

nav li:active {
  border-top: 1px outset rgb(100,100,100);
  border-bottom: 3px outset #c4c4c4;
  background: linear-gradient(90deg, slategray 0%, rgb(30, 30, 30) 3%, rgb(30, 30, 30) 98%, slategray 99%); 
}

nav li h3 {
  margin: 0px auto;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 0px;
  padding-right: 40px;
}


nav a {
  text-decoration: none;
  width: 100%;
  color: white;
  font-weight: bold;
}

.reflect-list-item {
  font-size: 24px;
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.reflect-list-item-p {
  font-size: 18px;
  text-decoration: none;
  color: black;
  padding-bottom: 32px;
}

.nav-right {
  position: absolute;
  top: 8px;
  right: 8px;
}


/* TIMELINE
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.timeline {
  width: 100%;
  overflow: hidden;
}

.time-wrapper {
  margin-right: 30px;
  width: 100px;
  max-width: 100px;
  top: 0px;
  align-self: flex-start;
  position: -webkit-sticky; /* Safari */
  position: sticky;
  line-height: 0.9;
  z-index: 1;
}

.time-wrapper h1 {
  font-family: Comorant Garamond;
  font-size: 60px;
}

.tl-year {
  display: flex;
  flex-direction: row;
  align-content: flex-start;
  align-items: stretch;
  justify-content: flex-start;
}

.tl-year h3 {
  font-size: 2rem;
  line-height: 1;
  margin: 15px 0;
}

.tl-semester {
  margin-top: 50px;
  padding-left: 20px;
  padding-right: 40px;
  border-left: 1px solid lightgray;
  display: flex;
  flex-direction: row;
  max-width: 700px;
}

.tl-semester-label {
  padding-top: 50px;
  margin-right: 30px;
  writing-mode: sideways-lr;
  text-align: right;
}

.tl-semester-label h3 {
  font-family: Coromant Garamond;
  font-size: 36px;
}

.tl-semester-classes {
  display: flex;
  flex-direction: column;
}

.tl-class {
  position:relative;
  margin-top: 10px;
  padding-left: 10px;
  padding-top: 10px;
  width: 600px;
  background-color: rgb(30,30,30);
  border-style: solid;
  border-color: rgb(100,100,100);
  border-width: 1px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px black;
  z-index:0;
}

.tl-class::after {
  background: radial-gradient(ellipse at center, transparent 0%, rgba(0, 0, 0, 0.7) 100%);
  content:"";
  position:absolute;
  z-index: 1;
  top:0; left:0;
  width: 100%;
  height: 100%;
}

.tl-class h2 {
  position: relative;
  display: inline;
  text-align: right;
  font-family: test;
  font-size: 50px;
  color: rgb(220,220,220);
  z-index: 5;
}

.tl-class h3 {
  position: relative;
  font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 20px;
  padding-left: 10px;
  color: rgb(220,220,220);
  z-index: 5;
}